import { Modal } from "react-bootstrap";
import "../../Styles/Lea.css";

const Sidebar = (props) => { 

    return (
        <div className="modalfade-sidebar">
            
        <Modal  show={props.show} onHide={props.handleClose} 
                id="modal-sidebar" className="modal-sidebar">
            <div className="modalcontent-sidebar">
        
                <Modal.Header className="border-0 pr-3 mr-2" closeButton>                
                </Modal.Header>

                <Modal.Body className="px-4 mx-5 mb-3 pb-0"                     
                style={{ borderRadius: "1rem", border: "1px solid #d4d3d1" }}>

                    <div >
                    <h5 className="pb-1 pl-1" 
                    >Visit More LinkedIn Products</h5>
                    </div>

                    <div className="side-container pb-0" 
                    >

                        <div className="icon-section-one pl-0" >
                            <div className="icons p-2" >
                                <img className="d-block m-auto"
                                src="data:image/svg+xml;base64," />
                                <span className="pt-1">Learning</span>

                            </div>
                            <div className="icons p-2">
                                <img className="d-block m-auto" src="data:image/svg+xml;base64," />
                                <span className="pt-1">Insights</span>

                            </div>
                            <div className="icons p-2">
                                <img className="d-block m-auto" src="data:image/svg+xml;base64," />
                                <span className="pt-1">Post a job</span>

                            </div>
                            <div className="icons p-2">
                                <img className="d-block m-auto" src="data:image/svg+xml;base64," />
                                <span className="pt-1">Advertise</span>

                            </div>
                        </div>

                        <div className="icon-section-one pl-0">
                            <div className="icons p-2">
                                <img className="d-block m-auto" src="data:image/svg+xml;base64," />
                                <span className="pt-1">Find Leads</span>

                            </div>
                            <div className="icons p-2">
                                <img className="d-block m-auto" src="data:image/svg+xml;base64," />
                                <span className="pt-1">Groups</span>

                            </div>
                            <div className="icons p-2">
                                <img className="d-block m-auto" src="data:image/svg+xml;base64," />
                                <span className="pt-1">ProFinder</span>

                            </div>
                            <div className="icons p-2">
                                <img className="d-block m-auto" src="data:image/svg+xml;base64," />
                                <span style={{ /* paddingLeft: "7px",  */ paddingTop: "1px" }}>Salary</span>

                            </div>
                        </div><br></br>
                    </div>
                    </Modal.Body>

{/* ======================Section Services================= */}

                <Modal.Body className="px-4 mx-5 mb-5 pb-0"                     
                style={{ borderRadius: "1rem", border: "1px solid #d4d3d1" }}>
                      {/*   <div className="services"> */}

                    <div>
                        <h5 className="pb-1" 
                        >LinkedIn Business Services</h5>
                    </div>


                        {/* <div className="title-two">
                            LinkedIn Business Services
                        </div> */}
                        <div className="list-services ">
                            <ul>
                                <li>
                                    <a href="">
                                        <span className="article">Talent Solutions</span><br></br>
                                        <span>Find,attract and recruit talent</span>
                                    </a>

                                </li>
                                <li>
                                    <a href="">
                                        <span className="article">Sales Solutions</span><br></br>
                                        <span>Unlock sales opportunities</span>
                                    </a>

                                </li>
                                <li>
                                    <a href="">
                                        <span className="article">Post a job for free</span><br></br>
                                        <span >Get your job in front of quality candidates</span>
                                    </a>

                                </li>
                                <li>
                                    <a href="">
                                        <span className="article">Marketing Solutions</span><br></br>
                                        <span>Acquire customers talent across your organization</span>
                                    </a>

                                </li>
                                <li>
                                    <a href="">
                                        <span className="article">Learning Solutions</span><br></br>
                                        <span>Develop talent across your organisation</span>
                                    </a>

                                </li>
                                <button className="create d-flex align-items-start pb-0">
                                    <h6 className="pt-2 mt-1 pr-1">Create a Company Page</h6> 
                                    <span style={{ fontSize: "26px"}}
                                    className=""
                                    >+</span>
                                </button>
                            </ul>

                        </div>


                  {/*   </div> */}

               
                    </Modal.Body>

                    </div>
           </Modal>
        </div>
    )
}

export default Sidebar;